<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="ÊèêÁ§∫"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">Âèñ Ê∂à</el-button>
          <el-button type="primary" @click="sure">Á°Æ ÂÆö</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { getSchool, getClass, getTeachers } from "data/listBody";

const dataOption = {
  leftMenu: [
    { id:'1', name:'Ê†°Âå∫Ôºö', placeholder:'ËØ∑ÈÄâÊã©Ê†°Âå∫', module:'schoolId', type:'select'},
    { id:'2', name:'Áè≠Á∫ßÔºö', placeholder:'ËØ∑ÈÄâÊã©Áè≠Á∫ß', module:'classId', type:'select'},
    { id:'3', name:'Â∏¶Áè≠ËÄÅÂ∏àÔºö', placeholder:'ËØ∑ÈÄâÊã©Â∏¶Áè≠ËÄÅÂ∏à', module:'teacherAccount', type:'select'},
    { id:'4', name:'Â≠¶ÂëòÂßìÂêçÔºö', placeholder:'ËØ∑ËæìÂÖ•Â≠¶ÂëòÂßìÂêç', module:'name'},
    { id:'5', name:'Â≠¶ÂëòË¥¶Âè∑Ôºö', placeholder:'ËØ∑ËæìÂÖ•Â≠¶ÂëòË¥¶Âè∑', module:'mobile'},
  ],
  rightMenu: [
    { id:'r1', name:'Êü•ËØ¢'},
    { id:'r2', name:'Êñ∞Â¢û'}
  ]
}

const dataTable = {
  table: [
    { label:'Ê†°Âå∫', prop:'schoolName'},
    { label:'Áè≠Á∫ß', prop:'className'},
    { label:'Â∏¶Áè≠ËÄÅÂ∏à', prop:'teacherName'},
    { label:'Â≠¶ÂëòÂßìÂêç', prop:'name'},
    { label:'Â≠¶ÂëòË¥¶Âè∑', prop:'account'},
  ],
  edit: [
    {name: 'ÁßØÂàÜÊòéÁªÜ', id: 't3'},
    {name: 'ÁºñËæë', id: 't1'},
    {name: 'Âà†Èô§', id: 't2'},
  ]
}

const tips = ['Á°ÆËÆ§Âà†Èô§ËØ•Êù°‰ø°ÊÅØ?'];

export default {
  name: 'studentList',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted() {
    this.search();
    this.dropInit();
  },
  activated(){
    this.search();
  },
  methods: {
    dropInit() {
      getSchool(res => {
        let { leftMenu } = this.dataOption;
        leftMenu = leftMenu.map(item => {
          if (item.module === "schoolId") item.options = res;
          return item;
        });
        this.dataOption.leftMenu = leftMenu;
      });
    },
    optchange(params){
      console.log(params.id + '--idË¢´ÂçïÂáª‰∫Ü')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`/student/add?type=new`);
    },
    tabchange(params){
      console.log(params.id + '--idË¢´ÂçïÂáª‰∫Ü')

      this.id = params.data.id;
      // ÁºñËæë
      if(params.id === 't1') return this.$router.push(`/student/add?type=edit&id=${this.id}`);
      // Âà†Èô§
      if(params.id === 't2') return this.dialogVisible = true;
      // ÁßØÂàÜÊòéÁªÜ
      if(params.id === 't3') return this.$router.push(`/student/detail?type=detail&id=${this.id}`);
    },
    sure(){
      this.api.delete(`/user/${this.id}`).then((res)=>{
        this.loading = false;
        this.search();
        this.$message(res.data);     
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false;
    },
    search(){  //ÊêúÁ¥¢
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };
      const params = Object.assign({}, optParams, pageParams, {
          roleId: '2'
      });
      const param = queryString.stringify(params || {});

      this.loading = true;
      this.api.get(`/user?${param}`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.setPage(res.data);
        this.tableList = res.data.list;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//Êï∞ÊçÆÂàùÂßãÂåñ
        const tableList = data.list.map(item => {
            if(item.beginTime && item.endTime)
            item.time = ScondDay(item.beginTime) + ' Ëá≥ ' + ScondDay(item.endTime);
        })
        this.tableList = tableList;
    }
  }
}
</script>
 
